import axios from "axios";

export class ServiceManager {
  getAllBooks = async (dispatch: any) => {
    await axios
      .get("/api/books")
      .then(response => {
        dispatch({ type: "GET_BOOKS", BookList: response.data });
      })
      .catch(err => {
        console.log(err.message);
      });
  };
  getBookById = async (dispatch: any, id: any) => {
    await axios
      .get("/api/books/" + id)
      .then(response => {
        dispatch({ type: "BOOK_DETAILS", selectedBook: response.data });
      })
      .catch(err => {
        console.log(err.message);
      });
  };

  addBooks = async (dispatch: any, book: any):Promise<any>=> {
    let token = "Bearer " + localStorage.getItem("login"); 
   console.log("token from addbook",token);
   
    let check = await axios.post("/api/books", book, {
      headers: { "Content-Type": "application/json", Authorization: token },
    });
    if (check.status === 200) {
      dispatch({ type: "ADDBOOK", addBook: book });
    
       return null;
    }else{
      return "error";
    } 
  };
  loginUser=async(dispatch:any,user:any)=>{
    try{
      let response=await axios.post("/api/users/login", user);
    console.log("Response",response.data);
    localStorage.setItem("login",response.data);
    dispatch({type:"LOGIN",payload:response.data});
    
    }catch(err:any){
      if(err){
        alert("Invalid Credentials");
      }
    }
  }

  deleteBook = async (dispatch: any, id: any) => {
    let token = "Bearer " + localStorage.getItem("login");
    let check = await axios.delete("/api/books/" + id, {
      headers: { "Content-Type": "application/json", Authorization: token },
    });
    {
      dispatch({ type: "DELETE", deleteBookId: id });
    }
  };
  registerUser=async(user: any, dispatch: any) =>{  
    let response=await axios.post("/api/users/register", user);
    console.log("Response",response.data);
    
    console.log("Rsponse in resister",response.data);
    dispatch({type:"REGISTER"});
  }
  getUsers = async (dispatch: any) => {
    await axios
      .get("http://localhost:8060/users")
      .then(response => {
        dispatch({ type: "USERS_LIST", Users: response.data });
      })
      .catch(err => {
        console.log(err.message);
      });
  };
  searchBooks = async (searchInput: any, selected: any, dispatch: any) => {
    if (selected === "id") {
      axios
        .get("/api/books/" + searchInput)
        .then(response => {
          dispatch({
            type: "SEARCH_BOOKS_BY_ID",
            searchedBookById: response.data,
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    } else if (selected === "author") {
      axios
        .get("/api/books/books/by/" + searchInput)
        .then(response => {
          dispatch({
            type: "SEARCH_BOOKS_BY_AUTHOR",
            searchedBooksByAuthor: response.data,
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    } else if (selected === "title") {
      axios
        .get("/api/books/by/title/" + searchInput)
        .then(response => {
          dispatch({
            type: "SEARCH_BOOKS_BY_TITLE",
            searchedBooksByTitle: response.data,
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    } else if (selected === "rating") {
      axios
        .get("/api/books/by/rating/" + searchInput)
        .then(response => {
          dispatch({
            type: "SEARCH_BOOKS_BY_RATING",
            searchedBooksByRating: response.data,
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    } else if (selected === "price") {
      axios
        .get("/api/books/priced/0/" + searchInput)
        .then(response => {
          dispatch({
            type: "SEARCH_BOOKS_BY_PRICE",
            searchedBooksByPrice: response.data,
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    }
  };
}


